import React from 'react'
import PropTypes from 'prop-types'
import NavDrawer, {
  DrawerHeader,
  DrawerSubtitle,
  DrawerTitle,
  DrawerContent,
} from '@material/react-drawer'
import DrawerList from './DrawerList'

const MobileDrawer = ({ isOpen, setIsDrawerOpen }) => (
  <NavDrawer
    className='app-drawer'
    modal
    open={isOpen}
    onOpen={() => setIsDrawerOpen(true)}
    onClose={() => setIsDrawerOpen(false)}
  />
)
MobileDrawer.propTypes = {
  isOpen: PropTypes.bool.isRequired,
  setIsDrawerOpen: PropTypes.func.isRequired,
}

const DesktopDrawer = () => (
  <NavDrawer
    className='app-drawer'
  />
)

const ResponsiveDrawer = ({ isMobile, isOpen, setIsDrawerOpen }) => (
  isMobile ? (
    <MobileDrawer
      isOpen={isOpen}
      setIsDrawerOpen={setIsDrawerOpen}
    />
  ) : (
    <DesktopDrawer />
  )
)
ResponsiveDrawer.propTypes = {
  isMobile: PropTypes.bool.isRequired,
  isOpen: PropTypes.bool.isRequired,
  setIsDrawerOpen: PropTypes.func.isRequired,
}

const Drawer = ({ isMobile, isOpen, setIsDrawerOpen }) => (
  <ResponsiveDrawer
    isMobile={isMobile}
    isOpen={isOpen}
    setIsDrawerOpen={setIsDrawerOpen}
  >
    <DrawerHeader>
      <DrawerTitle>
        {isMobile ? 'Mobile Drawer Title' : 'Mobile Desktop Title'}
      </DrawerTitle>
      <DrawerSubtitle>
        {isMobile ? 'Mobile Drawer Subtitle' : 'Mobile Desktop Subtitle'}
      </DrawerSubtitle>
    </DrawerHeader>
    <DrawerContent>
      <DrawerList
        onItemClick={() => setIsDrawerOpen(false)}
      />
    </DrawerContent>
  </ResponsiveDrawer>
)
Drawer.propTypes = {
  isMobile: PropTypes.bool.isRequired,
  isOpen: PropTypes.bool.isRequired,
  setIsDrawerOpen: PropTypes.func.isRequired,
}

export default Drawer
